<template>
  <div class="container">
    <IndexHeader>在线二维码生成（QR-Code）</IndexHeader>
    <QrEditor :qrText="qrText">
      <template #contentTitle>输入内容，开始制作二维码</template>
      <template #tips>功能描述：将文本或网址转为二维码。</template>
      <template #help>
        <p><span>1. 有效期：</span>本页面【文本/网址二维码】是静态二维码，永久免费，且永久有效。</p>
        <p>
          <span>2. 容量限制</span>：静态二维码最多可容纳 2953 字节（约2.9KB）的数据。本站使用的是 UTF-8
          编码，其中一个数字/字母 = 1个字节，一个汉字 = 3个字节
        </p>
        <p>【静态码】：直接将文字存储在二维码中，容量有限（2953 字节）。而且内容变化二维码必变。</p>
        <p>【活码】：将二维码内容存储在云端数据库中，理论上容量无限大。可以实现内容变化但二维码不变。</p>
      </template>
      <template #content>
        <a-textarea
          v-model:value="qrText"
          placeholder="请输入文本或网址，支持 emoji"
          :autoSize="{ minRows: 4, maxRows: 6 }"
          :maxlength="3000"
          allowClear
        />
      </template>
    </QrEditor>
  </div>
</template>

<script>
import IndexHeader from '@/components/IndexHeader.vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';
import { ref } from 'vue';
import { useHead } from '@vueuse/head';

export default {
  name: 'Index',
  components: {
